.header {
    padding-bottom:20px;padding-top:20px;
   }
.fotoDepan {
       display:block;width:150px;border-radius:80px;margin:0 auto;
      }
.p1Style{
    margin:0 auto;font-family:'Manrope', sans-serif;text-align:center;color:darkslategray;padding-top:10px;
   }
