  }
.table1Container
  {
   max-width:350px;padding-top:20px;padding-bottom:10px;margin:0 auto;
  }
.table1 {
   border-collapse:collapse;vertical-align:middle;width:100%;
  }
td {
border:1px solid darkslategray;
}
.theadText {
      font-family:'Manrope', sans-serif;text-align:center;
     }
.tbodyText {
      font-family:'Manrope', sans-serif;text-align:center;
     }   
.listContainer {
          display:block;display:flex;flex-direction:row;align-items:flex-start;
         }
