      font-family:'Manrope', sans-serif;list-style-position:outset; position:relative;right:7px;
     }
@media all and (max-width:431px) {
        .h2Style{
                 max-width:250px;
                }
       }
@media all and (max-width:260px){
       .linkContainer{
                      flex-flow:column nowrap;
