.hnoContainer {
                max-width:320px;padding-top:30px;
                display:flex;flex-flow:row nowrap;justify-content:flex-end;border:1px none;
              }
.hasilOutput {
                color:darkslategray;max-width:320px; height:30px;display:flex;flex-flow:row nowrap;font-size:30px;font-family:'Inconsolata';border:1px none;
             }
.hrStyle2 {
                max-width:320px;font-size:1px;background-color:lightgoldenrodyellow;
               }
.deleteContainer {
                    max-width:320px;height:50px;display:flex;flex-flow:row nowrap;justify-content:space-between;align-items:center;
                    border:1px none;
                 }
.clearAll {
                    font-size:30px;color:red;font-family:'Inconsolata';display:flex;flex-flow:row nowrap;justify-content:center;align-items:center;
                    position:relative;left:30px;height:23px;border:1px none;
          }
.delete {
        font-size:30px;color:red;font-family: 'Inconsolata';display:flex;flex-flow:row nowrap;justify-content:center;align-items:center;
        position:relative;right:30px;border:1px none;height:23px;
        }
.boxContainer {
            max-width:320px;height:320px;border:1px solid darkslategray;
            display:grid;grid-template-columns:repeat(auto-fit,minmax(70px,1fr));
            justify-items:center;align-items:center;
          }
.boxNDisplay2 {
            color:darkslategray;width:70px;height:70px;box-sizing:content-box;text-align:center;border:1px solid darkslategray;border-radius:50%/40%;
            display:flex;flex-flow:column nowrap;justify-content:center;align-items:center;
            font-size:2em;font-family: 'Inconsolata';
              }
.boxNDisplay1 {
      color:darkslategray;width:70px;height:70px;box-sizing:content-box;text-align:center;border:1px solid darkslategray;border-radius:50%/40%;
      display:flex;flex-flow:column nowrap;justify-content:center;align-items:center;
      font-size:2em;font-family: 'Inconsolata'; 
    }
.riwayatProject {
        color:darkslateblue;padding-top:10px;padding-bottom:10px;
       }
.paragrafRiwayat {
        max-width:490px;
       }
.riwayatProject ol:nth-of-type(1) {
                            max-width:450px;
                           }
             div.boxNDisplay1:hover {
                            cursor:pointer;
                           }
             div.boxNDisplay2:hover {
                            cursor:pointer;
                           }
             div.clearAll {
                           cursor:pointer;
                          }
             div.delete:hover {
                               cursor:pointer;
                              }
             details:hover {
                     cursor:pointer;
                    }
             summary:focus {
                            border:0px none;
                           }
    @media all and (min-width:450px){
                                        .paragrafRiwayat {
                                                        text-align:justify;
                                                        }
                                        .riwayatProject ol:nth-of-type(1) {
